.moradiaContainer {
    padding-top: 3%;
    background-color: whitesmoke;
    color: black;    
}

.moradiaCarouselImg {
    position: relative;
    min-width: 100%;    
   
    
}

.carousel-control-next,
.carousel-control-prev /*, .carousel-indicators */ {
    filter: invert(100%);
}

.carousel{
    justify-content: center;
    margin-left: 12%;
    padding-right: 8%;
    padding-left: 8%;
    margin-top: 2%;
    max-height: min-content;
}

/* Extra small devices (portrait phones, less than 576px)*/
@media (max-width: 575.98px) {
    .rows{
        font-size: 14px;
    }
    .moradiaContainer {
        padding-left: 5%;
        padding-right: 5%;
    }
}

/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) and (max-width: 767.98px) {
    .rows{
        font-size: 14px;
    }
    .moradiaContainer {
        padding-left: 5%;
        padding-right: 5%;
    }
}


/* Medium devices (tablets, 768px and up)*/
@media (min-width: 768px) and (max-width: 991.98px) {
    .rows{
        font-size: 18px;
    }
    .moradiaContainer {
        padding-left: 20%;
        padding-right: 20%;
    }
    .moradia-btn {
        margin-left: -25%;
    }   
}

/* Large devices (desktops, 992px and up)*/
@media (min-width: 992px) and (max-width: 1199.98px) {
    .rows{
        font-size: 18px;
    }
    .moradiaContainer {
        padding-left: 20%;
        padding-right: 20%;
    }
    .moradia-btn {
        margin-left: -25%;
    }
}

/* Extra large devices (large desktops, 1200px and up)*/
@media (min-width: 1200px) {
    .rows{
        font-size: 18px;
    }
    .moradiaContainer {
        padding-left: 20%;
        padding-right: 20%;
    }
    .moradia-btn {
        margin-left: -25%;
    }
}
