#login {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-top: 5%;
    color: white;
}

#login .form {
    width: 35%;
    margin: 1% auto;
    background-color: white;
    border-radius: 20px;
    padding-bottom: 2%;
    color: black;
}

.field {
    padding-top: 5%;
    text-align: center;
}
#login .field label {
    display: block;
}

.cpf-input {
    width: 50%;
}

.password-input {
    width: 50%;
}

.actions {
    text-align: center;
    margin-top: 5%;
}