.component1Title {
    padding-top: 10%;
    padding-left: 10%;
    line-height: normal;
    color: white;
    font-family: 'Roboto', sans-serif;
}
.component1Subtitle {
    padding-left: 10%;
    line-height: normal;
    color: white;
    font-family: 'Roboto', sans-serif;
}
.homeComponent1{
    background: url(../../img/4.svg);
    background-repeat: no-repeat;
}


/* Extra small devices (portrait phones, less than 576px)*/
@media (max-width: 575.98px) {
    .component1Title {
        font-size: 25px;
        font-weight: 900;
    }
    .component1Subtitle {
        font-size: 15px;
    }  
    .homeComponent1{
        background-size: cover;
        height: 250px;
    }
}

/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) and (max-width: 767.98px) {
    .component1Title {
        font-size: 32px;
        font-weight: 900;
    }
    .component1Subtitle {
        font-size: 16px;
    }
    .homeComponent1{
        background-size: 100% auto;
        background-attachment: fixed;
        height: 300px;
    }
}


/* Medium devices (tablets, 768px and up)*/
@media (min-width: 768px) and (max-width: 991.98px) {
    .component1Title {
        font-size: 45px;
        font-weight: 900;
    }
    .component1Subtitle {
        font-size: 20px;
    }
    .homeComponent1{
        background-size: 100% auto;
        background-attachment: fixed;
        height: 400px;
    }
}

/* Large devices (desktops, 992px and up)*/
@media (min-width: 992px) and (max-width: 1199.98px) {
    .component1Title {
        font-size: 50px;
        font-weight: 900;
    }
    .component1Subtitle {
        font-size: 22px;
    }
    .homeComponent1{
        background-size: 100% auto;
        background-attachment: fixed;
        height: 500px;
    }
}

/* Extra large devices (large desktops, 1200px and up)*/
@media (min-width: 1200px) {
    .component1Title {
        font-size: 72px;
        font-weight: 900;
    }
    .component1Subtitle {
        font-size: 24px;
    }
    .homeComponent1{
        background-size: 100% auto;
        background-attachment: fixed;
        height: 800px;
    }
}
