.homeComponent2 {
    background-color: white;
    padding-bottom: 5%;
}

.component2Title {
    padding-top: 5%;
    text-align: center;
    line-height: normal;
    color: #008000;
    font-family: 'Roboto', sans-serif;
}
.component2Subtitle {
    padding-top: 1%;
    text-align: center;
    line-height: normal;
    color: black;
    font-family: 'Roboto', sans-serif;
}

.component2Cards {
    margin: 5% auto;
    justify-content: center;
}
.card-button {
    position:absolute;
    bottom: 10%;
    left: 15%;
    width: 70%;
    font-size: 16px;
    cursor: pointer;
    align-self: center;
}


/* Extra small devices (portrait phones, less than 576px)*/
@media (max-width: 575.98px) {
    .component2Title {
        font-size: 25px;
        font-weight: bolder;
    }
    .component2Subtitle {
        font-size: 15px;
    }  
    .component2Cards{
        display: grid;
        
    }
}

/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) and (max-width: 767.98px) {
    .component2Title {
        font-size: 36px;
        font-weight: bolder;
    }
    .component2Subtitle {
        font-size: 16px;
    }
    .component2Cards{
        display: grid;
    }
}


/* Medium devices (tablets, 768px and up)*/
@media (min-width: 768px) and (max-width: 991.98px) {
    .component2Title {
        font-size: 36px;
        font-weight: bolder;
    }
    .component2Subtitle {
        font-size: 20px;
    }
    .component2Cards{
        display: flex;
    }
}

/* Large devices (desktops, 992px and up)*/
@media (min-width: 992px) and (max-width: 1199.98px) {
    .component2Title {
        font-size: 40px;
        font-weight: bolder;
    }
    .component2Subtitle {
        font-size: 22px;
    }
    .component2Cards{
        display: flex;
    }
}

/* Extra large devices (large desktops, 1200px and up)*/
@media (min-width: 1200px) {
    .component2Title {
        font-size: 60px;
        font-weight: bolder;
    }
    .component2Subtitle {
        font-size: 24px;
    }
    .component2Cards{
        display: flex;
    }
}
